import { Component } from '@angular/core';
import { HttpClient, HttpRequest, HttpEventType, HttpResponse, HttpErrorResponse } from '@angular/common/http'

@Component({
    selector: 'UploadComponent',
    template: require('./upload.component.html')
})
export class UploadComponent {
    public progress: number=0;
    public message: string='';
    constructor(private http: HttpClient) { }

    upload(files: any) {
        if (files.length === 0)
            return;

        const formData = new FormData();

        for (let file of files)
            formData.append(file.name, file);

        const uploadReq = new HttpRequest('POST', `api/upload`, formData, {
            reportProgress: true,
        });

        this.http.request(uploadReq).subscribe(res => {
            if (res.type === HttpEventType.UploadProgress)
                this.progress = Math.round(100 * res.loaded );
            else if (res.type === HttpEventType.Response)
                this.message = res.status.toString();
        });
    }
}



