import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';  // replaces previous Http service

import { HttpClient, HttpRequest, HttpEventType, HttpResponse, HttpErrorResponse } from '@angular/common/http'
import { CommonModule } from "@angular/common";
import { TooltipModule } from 'ngx-bootstrap';
import { AlertModule } from 'ngx-bootstrap';
import { CarouselModule } from 'ngx-bootstrap/carousel';
import { UploadComponent } from './components/Upload/component.upload';

import { headerComponent } from './components/shared/header/header.component';

import { AppComponent } from './components/app/app.component';
import { NavMenuComponent } from './components/navmenu/navmenu.component';
import { HomeComponent } from './components/home/home.component';
import { FetchDataComponent } from './components/fetchdata/fetchdata.component';
import { CounterComponent } from './components/counter/counter.component';
import { contactsComponent } from './components/contacts/contacts.component';
import { sellerComponent } from './components/seller/seller.component';
import { renterComponent } from './components/renter/renter.component';
import { ListingsService } from './components/home/listings.service';
import { SearchFilterPipe } from './components/home/SearchFilter.Pipe';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

@NgModule({
    declarations: [
        AppComponent,
        NavMenuComponent,
        CounterComponent,
        FetchDataComponent,
        HomeComponent,
        headerComponent,
        contactsComponent,
        sellerComponent,
        renterComponent,
        UploadComponent,
        SearchFilterPipe
    ],

    providers: [ListingsService],
    imports: [
        CommonModule,
        HttpClientModule,
  
        FormsModule,
        TooltipModule.forRoot(), 
        AlertModule.forRoot(),
        CarouselModule.forRoot(),
        [BrowserModule, FormsModule, ReactiveFormsModule],
        RouterModule.forRoot([
            { path: '', redirectTo: 'home', pathMatch: 'full' },
            { path: 'home', component: HomeComponent },
            { path: 'counter', component: CounterComponent },
            { path: 'fetch-data', component: FetchDataComponent },
            { path: 'contacts', component: contactsComponent },
            { path: 'seller', component: sellerComponent },
            { path: 'renter', component: renterComponent },

            { path: '**', redirectTo: 'home' }
        ])
    ]
})
export class AppModuleShared {
}



