import { Component, OnInit, Injectable, Inject} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { HttpClient, HttpRequest, HttpEventType, HttpResponse, HttpErrorResponse } from '@angular/common/http'
import { iListings } from './Listings.component';
@Injectable()
export class ListingsService {
    public thisurl: string ="";
    public alllistings: iListings[];

        ///http.get(originUrl + 'YOUR API URI HERE').subscribe(result => {...});
    //,@Inject('BUILD_URL') originUrl:string
    constructor(private http: HttpClient  ) {
    //    this.thisurl = originUrl;
        console.log( '/Listings.json')
        console.log('inside Listings sevrvice  constructor')
                }
    private handleerror(error: Response) {
        console.log(error);
        let message = 'Error status code ${error.status} at ${error.url}' ;
        return Observable.throw( message );
    }
    getListings():Observable<iListings[]>
    {
        console.log('inside the service ')
        return this.http.get('/Listings.json')
            
            .map((res: any) => <iListings[]>res.json())//you can use as iListings[] too
           // .do(d => console.log(this.alllistings))
            .catch(this.handleerror)
            }
   
     }

   


