import { Component, Inject } from '@angular/core';
import { HttpClient, HttpRequest, HttpEventType, HttpResponse, HttpErrorResponse } from '@angular/common/http'
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';

@Component({
    selector: 'fetchdata',
    template: require('./fetchdata.component.html')
})
export class FetchDataComponent {
    public forecasts: WeatherForecast[];

    constructor(http: HttpClient, @Inject('BASE_URL') baseUrl: string) {
        http.get<WeatherForecast[]>(baseUrl + 'api/SampleData/WeatherForecasts')
            .subscribe(res => {
                //  console.log(res);
                this.forecasts = res;
                console.log(this.forecasts);
            },
            err => {
                console.log("Error occured");
                this.handleError(err);
            },
                () => {
                    console.log("The POST Upload Files is now completed.");
                });

    }
    private handleError(error: HttpErrorResponse) {
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('An error occurred:', error.error.message);
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.error(
                'Backend returned code ${error.status}, ' +
                'body was: ${error.error}');
        }
        // return an ErrorObservable with a user-facing error message
        return new ErrorObservable(
            'Something went wrong; please try again later.');
    };
}
interface    WeatherForecast {
    dateFormatted: string;
    temperatureC: number;
    temperatureF: number;
    summary: string;
}



